.vf-slider {
  position: relative;
}

.vf-slider__item {
  position: relative;
  height: auto;
  display: flex;
  border: 1px solid #ececec;
}

@media (max-width: 575.98px) {
  .vf-slider__item {
    flex-direction: column;
  }
}

.vf-slider__image {
  flex-basis: 40%;
}

@media (max-width: 575.98px) {
  .vf-slider__image {
    flex-basis: initial;
  }
}

.vf-slider__content {
  flex-basis: 60%;
  padding: 20px;
  padding: 1.25rem;
}

@media (max-width: 575.98px) {
  .vf-slider__content {
    flex-basis: initial;
  }
}

.vf-slider__cta {
  margin-top: 20px;
  margin-top: 1.25rem;
}
